import React, { Component } from 'react';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import Home from './containers/Home';
import { BrowserRouter as Router, Route } from "react-router-dom";
import {
    Navbar,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink} from 'reactstrap';

class App extends Component {
    render() {
    return (
        <Router>
            <div>
                <Route exact path={process.env.PUBLIC_URL + '/'} component={Home}/>
                <Navbar fixed="top">
                    <NavbarBrand>
                        <a href={process.env.PUBLIC_URL + '/'}>
                            <h3>OLIVER DOWD</h3>
                        </a>
                    </NavbarBrand>
                    <Nav>
                        <NavItem className='nav-right'>
                            <NavLink href={process.env.PUBLIC_URL + '/#professional'}>
                                <h5>PROFESSIONAL</h5>
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href={process.env.PUBLIC_URL + '/#projects'}>
                                <h5>PROJECTS</h5>
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href={process.env.PUBLIC_URL + '/#lifestyle'}>
                                <h5>LIFESTYLE</h5>
                            </NavLink>
                        </NavItem>
                    </Nav>
                </Navbar>
            </div>
        </Router>
    );
    }
}

export default App;



