import React from 'react';
import './Professional.css'
import '../index.css'

export default class Professional extends React.Component {
    carouselNav (index) {
    }

    render () {
            return (
                <div class="container py-5" id="professional">
                    <div class="row section-container">
                        <div className="col-md-12 col-lg-5 section-cell">
                            <h1 className="section-title">PROFESSIONAL</h1>
                        </div>
                        <div className="col-md-12 col-lg-7 px-4 border-left border-light">
                            <h1 className="section-sub-title">EXPERIENCE</h1>
                            <div className="experience-card">
                                <h5 className="experience-card-title">Software Developer</h5>
                                <h5 className="experience-card-company">Freshii</h5>
                                <h6 className="experience-card-date">September 2018 - December 2018</h6>
                            </div>
                            <div className="experience-card">
                                <h5 className="experience-card-title">Catastrophe Modeling Application
                                    Developer</h5>
                                <h5 className="experience-card-company">Validus Research</h5>
                                <h6 className="experience-card-date">January 2018 - April 2018</h6>
                            </div>
                            <div className="experience-card">
                                <h5 className="experience-card-title">Data Analyst</h5>
                                <h5 className="experience-card-company">CIBC Capital Markets</h5>
                                <h6 className="experience-card-date">January 2017 - April 2017</h6>
                            </div>
                            <div className="experience-card">
                                <h5 className="experience-card-title">Data Analyst</h5>
                                <h5 className="experience-card-company">Adknown</h5>
                                <h6 className="experience-card-date">January 2016 - April 2016</h6>
                            </div>

                            <h1 className="section-sub-title mt-5">SKILLS</h1>
                            <div className="skill-card">
                                <h5 className="skill-card-name">Python</h5>
                            </div>
                            <div className="skill-card">
                                <h5 className="skill-card-name">C++</h5>
                            </div>
                            <div className="skill-card">
                                <h5 className="skill-card-name">SQL</h5>
                            </div>
                            <div className="skill-card">
                                <h5 className="skill-card-name">HTML</h5>
                            </div>
                            <div className="skill-card">
                                <h5 className="skill-card-name">CSS</h5>
                            </div>
                            <div className="skill-card">
                                <h5 className="skill-card-name">Javascript</h5>
                            </div>
                        </div>
                    </div>
                </div>
            )
    }
}


