import React from "react";
import "./Projects.css";

export default class Project extends React.Component {

    render () {
        return (
            <div className="container py-5" id="projects">
                <div className="row section-container">
                    <div className="col-md-12 col-lg-5 section-cell">
                        <h1 className="section-title">PROJECTS</h1>
                    </div>
                    <div className="col-md-12 col-lg-7 px-4 border-left border-light">
                        <div class="project-card">
                            <h1 className="project-card-title">ESTUARY LIFE</h1>
                            <h5 className="project-card-description">Estuary life is a medical marijuana retail site created on Squarespace. I was contracted to work with a professional designer on this project and implement the design that they had created.</h5>
                        </div>


                    </div>
                </div>
            </div>
        )
    }
}


