import React, { Component } from 'react';
import Cover from './Cover';
import Professional from './Professional';
import Lifestyle from './Lifestyle';
import Projects from './Projects';


class App extends Component {
    render() {
        return (
            <div>
                <Cover/>
                <Professional/>
                <Projects/>
                <Lifestyle/>
            </div>
        );
    }
}

export default App;



