import React from 'react';
import "./Lifestyle.css";
import summer2018BikingCover from "../images/summer-2018-biking-cover.jpg";
import climbingCover from "../images/climbing-cover.jpg";
import exploreCover from "../images/explore-cover.jpg";
import campingCover from "../images/camping-cover.jpg"

export default class Lifestyle extends React.Component {

    render () {

        return (
            <div className="container py-5" id="lifestyle">
                <div className="row section-container">
                    <div className="col-md-12 col-lg-5 section-cell">
                        <h1 className="section-title">LIFESTYLE</h1>
                    </div>
                    <div className="col-md-12 col-lg-7 px-4 border-left border-light">
                        <div className="container">
                            <div className="row">
                                <div className="col-md-6 col-sm-6 mb-4">
                                    <div className="hobby-card pointer" id="hobby-card-cycling">
                                        <div className="hobby-card-content">
                                            <img className="landscape" alt="img" src={summer2018BikingCover}></img>
                                            <h1 className="center">Bike</h1>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6 col-sm-6 mb-4">
                                    <div className="hobby-card pointer" id="hobby-card-cycling">
                                        <div className="hobby-card-content">
                                            <img className="landscape" alt="img" src={climbingCover}></img>
                                            <h1 className="center">Climb</h1>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6 col-sm-6 mb-4">
                                    <div className="hobby-card pointer" id="hobby-card-cycling">
                                        <div className="hobby-card-content">
                                            <img className="landscape" alt="img" src={exploreCover}></img>
                                            <h1 className="center">Explore</h1>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6 col-sm-6 mb-4">
                                    <div className="hobby-card" id="hobby-card-cycling">
                                        <div className="hobby-card-content">
                                            <img className="landscape" alt="img" src={campingCover}></img>
                                            <h1 className="center">Camp</h1>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


